import { createRouter, createWebHistory, createWebHashHistory } from 'vue-router'
import Home from '../views/Home.vue'
import Vrijwilliger from '../views/Vrijwilliger.vue'
import FotoInfo from '../views/FotoInfo.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/Vrijwilliger',
    name: 'Vrijwilliger',
    component: Vrijwilliger
  },
  {
    path: '/FotoInfo',
    name: 'FotoInfo',
    component: FotoInfo
  },
]

const router = createRouter({
  mode: 'history',
  history: createWebHashHistory(),
  routes
})

export default router
