<template>
<v-card style="border-color: #3F816C; border-width: 3px; padding: 3px;">
    <v-card style="border-color: #3F816C; border-width: 1px;">
        <v-tabs v-model="selected_query_obj_path" style="height: min-content;" show-arrows centered center-active>
            <v-tab  v-for="(query_obj, index) in availableRequests" :key="index" :value="query_obj.path" >
                <span class="truncate" style="width: 105px; font-size: smaller;"  >{{query_obj.title}}</span>
            </v-tab>
        </v-tabs>
    </v-card >
    <v-window v-model="selected_query_obj_path" style="resize: vertical; height: 500px; " >
        <div v-for="(query_obj, index) in availableRequests" :key="index">
            <v-window-item  :value="query_obj.path">
                <QueryOutputTab :query_obj="query_obj" />
            </v-window-item>
        </div>
    </v-window>
</v-card>

<!-- <select v-model="selected_query_obj_path">
    <option v-for="(query_obj, index) in availableRequests" :key="index" :value="query_obj.path">{{query_obj.title}}</option>
</select>
<div v-if="(selectedQueryObj != undefined)">
    <QueryOutputTab :query_obj="selectedQueryObj" />
</div> -->
</template>

<script>
import QueryOutputTabVue from './QueryOutputTab.vue';
import QueryOutputTab from './QueryOutputTab.vue';
export default {
    name: 'ResponseOutput',
    components: {
    QueryOutputTabVue,
    QueryOutputTab
    },
    data (){
        return {
            selected_query_obj_path: 'similar_targets',
            buttoncolor: '#3F816C', 

        }
    },
    methods: {
    },
    computed: {
        availableRequests(){
            return this.$store.state.home.query_objects.filter((e) => {return e.shown == true})
        },
        selectedQueryObj (){
            const index = this.$store.state.home.query_objects.map((e) => {return e.path}).indexOf(this.selected_query_obj_path)
            if (index != -1) {
                return this.$store.state.home.query_objects[index]
            }
            else {
                return undefined
            }
        },
        lastRequestedPath(){
            return this.$store.state.home.last_clicked_query_object_path
        },
    },
    watch:{
        lastRequestedPath(new_value, old_value){
            this.selected_query_obj_path = new_value
        },
        selected_query_obj_path(new_value, old_value){
            this.$store.commit('setSelectedTabPath', new_value)
        }
    }
}
</script>

<style>
.scrollable{
  overflow-y: scroll;
}
.list{
  border: 1px solid steelblue;
  padding: 5px;
  margin: 2px;
  height: 200px;
}
.box {
    border: solid;
    outline-color: gray;
    border-width: 1px;
    padding: 3px;
    margin-top: 3px;
    margin-bottom: 3px;
    font-size: smaller;
}
.truncate {
    display: inline-block;
    overflow:hidden;
    text-overflow: ellipsis;
    white-space: pre-line;
}
</style>