<template>
<div>

    <!-- if a query_obj is sending, display loading, if nothing is sending and nothing recieved: you did not send request, if not sending and has response: display query_obj that has a response title -->
        <p v-if="(query_obj.sending == true)">Loading ...</p>
        <p v-else-if="(query_obj.response.length == 0)">Did not send request</p>
        <p v-else>success!</p>
    <div>
        Verstuurd: <input :value="query_obj.send_ids" readonly/>
        <v-expansion-panels class="scrollable list" style="height: 380px;" variant="accordion" multiple>
            <div v-if="(query_obj.response.length !== 0)">
                <!-- for every returned id, create a list item that handles descriptions -->
                <div v-for="(listItem , index) in listItems" :key="index">
                    <OutputListItem :item_obj="listItem" size="w-100" />
                    <!-- <hr noshade="true" size="2px"> -->
                </div>
            </div>
        </v-expansion-panels>
    </div>
    <div >
    <v-row>
            <v-sheet class="pa-0 my-4 ml-4" width="250px">
                <v-text-field hide-details :model-value="listIds" variant="outlined" readonly class="pa-0"></v-text-field>
            </v-sheet>
            <v-sheet class="pa-2 my-4">
                <v-btn @click="copyText(listIds)"  >Copy ids</v-btn>

            </v-sheet>
    </v-row>

    </div>
</div>
</template>

<script>
import OutputListItem from './OutputListItem'

export default {
    name: 'QueryOutputTab',
    components: {
    OutputListItem,
    },
    props: {
        query_obj: Object,
    },
    methods: {
        copyText(text) {  
            const el = document.createElement('textarea');  
            el.value = text;                    
            el.setAttribute('readonly', '');                
            el.style.position = 'absolute';                     
            el.style.left = '-9999px';                      
            document.body.appendChild(el);                  
            const selected =  document.getSelection().rangeCount > 0  ? document.getSelection().getRangeAt(0) : false;                                    
            el.select();                                    
            document.execCommand('copy');                   
            document.body.removeChild(el);                  
            if (selected) {                                 
            document.getSelection().removeAllRanges();    
            document.getSelection().addRange(selected);   
            }
        },
        listItemTooltip(listItem){
            if (listItem.type == 'target') {
                return 'Question Ids: ' + listItem.question_ids
            } else 
            if (listItem.type == 'question') {
                return 'Target Id: ' + listItem.target_id
            }
        },
    },
    computed: {
        // list the response items (this list is being displayed in the list), this formats the returned data according to the returned data type
        listItems (){
            if (this.query_obj.response.length != 0) {
                if (this.query_obj.returns == 'target_list') {
                    var returned_target_data = this.query_obj.response.map((e) => {return this.$store.getters.targetDataFromId(e)})
                    return returned_target_data
                } else 
                if (this.query_obj.returns == 'question_list') {
                    var returned_question_data = this.query_obj.response.map((e) => {return this.$store.getters.questionDataFromId(e)})

                    return returned_question_data
                } else
                if (this.query_obj.returns == 'target') {
                    // var returned_target_data = this.$store.state.home.target_data.filter((e) => {return this.query_obj.response.includes(e.id.parseInt())})
                    var returned_target_data = this.$store.getters.targetDataFromId(this.query_obj.response[0])
                    return [returned_target_data]
                } else 
                if (this.query_obj.returns == 'question') {
                    const returned_question_data = this.$store.getters.questionDataFromId(this.query_obj.response.Question_ID)
                    // this.$store.commit('setAnswerDistributionFromId', {'id': returned_question_data.id, 'answer_distribution': query_obj.response.answer_distribution})
                    
                    // const updated_question_data =  this.$store.getters.questionDataFromId(this.query_obj.response.Question_ID)
                    
                    return [returned_question_data]
                }
            } else {
                return []
            }  
        },
        listIds () {
            if (this.query_obj.response.length != 0) {
                return this.listItems.map((e) => {return e.id})
            } else {
                return []
            }
        },
        type (){

        }
    },
    watch: {
        listIds (new_value, old_value){
            if (this.query_obj.returns == 'target' || this.query_obj.returns == 'target_list') {
                this.$store.commit('setOutputListItems', {value: new_value, type: 'target'})
            } else 
            if (this.query_obj.returns == 'question' || this.query_obj.returns == 'question_list') {
                this.$store.commit('setOutputListItems', {value: new_value, type: 'question'})
            }
        }
    }
}
</script>

<style>
.scrollable{
  overflow-y: scroll;
}
.list{
  border: 1px solid steelblue;
  padding: 5px;
  margin: 2px;  
}
.box {
    border: solid;
    outline-color: gray;
    border-width: 1px;
    padding: 3px;
    margin-top: 3px;
    margin-bottom: 3px;
    font-size: smaller;
}
.custom-text-field.v-text-field.v-text-field--enclosed .v-input__slot {
  padding: 0;
}
</style>