
<template>
<div>
    <div >
    <!-- input search field -->
        <v-col>
            <v-text-field variant="outlined"
                            persistent-hint
                            v-on:keydown.enter="clickItem(filteredList[0])" 
                            label="Doorzoek Lijst..." 
                            v-model="search" 
                            hint="Voeg meerdere filters toe met een komma, enter om bovenste te selecteren"></v-text-field>
        </v-col>
    </div>
    <div>
            <v-row>
                <v-col >
                    <v-card class="pa-2 ma-2" variant="outlined" @click="toggleAll(filteredList.map((e) => {return e.id}))" :color="buttoncolor">alles in-/uitschakelen</v-card>

                    <v-select class="ml-2" style="width:10em;" label="Sorteer op:" density="compact" outlined :items="sort_types" v-model="selected_sort_type"></v-select>
                </v-col>

                <v-col>
                    <v-card class="ma-2" @click="selectAll(filteredList.map((e) => {return e.id}))">
                        <v-checkbox-btn  :model-value="allSelected" label="Selecteer Alles" />
                    </v-card>
                    <v-card class="ma-2" @click="clearAll(filteredList.map((e) => {return e.id}))">
                        <v-checkbox-btn :model-value="noneSelected" label="Verwijder Alles" />
                    </v-card>
                </v-col>

            </v-row>
    <div>{{this.$store.state.home.selected_item_ids[selection_type].length}} GESELECTEERD</div>
    </div>

    <v-list @scroll="handleScrollInput" select-strategy="multiple" density="compact" class="scrollable list pa-0" style="resize: vertical; height: 30vh" size="h-50" v-model="selectedItemIds">

        <!-- create an input list item for every target or quetion (depending on filtered list (depending on listOfItemType)) -->
        <!-- clickItem is a veux muation that adds or removes (if already selected) an id from selected_item_ids -->
        <!-- link that input to selectItems, selectItems is the model that changes the veux store selected_items_ids, and checks the checkbox if in that list -->
        <v-list-item @click="clickItem(filter.id); updateInputBox()" 
                    v-for="(filter, index) in smallerFilteredList" :key="index"
                    :color="buttoncolor"
                    :value="filter.id"
                    class="pa-0 pl-1 ma-0"
                    >
            <v-row >
                <v-list-item-action start style="float: left;">
                    <v-checkbox-btn readonly :value="filter.id"  :color="buttoncolor" v-model="selectedItemIds" class="pt-1" /> 
                </v-list-item-action>
            </v-row>

            <div>
                    <v-list-item-title style="" class="truncate">{{filter.id}} {{filter.label}}</v-list-item-title>
            </div>


        </v-list-item>
    </v-list>
    <v-text-field v-model="input_box" v-on:input="updateSelectedItemsFromInputBox"/>
    <p>Selected: </p>
    <div class="list scrollable" style="height: 150px; resize: vertical; ">
        <v-list select-strategy="multiple" class="pa-0" density="compact">
            <v-list-item v-for="(filter, index) in selected_items[selection_type]" :key="index"
                        :color="buttoncolor"
                        :value="filter.id"
                        @click="clickItem(filter.id)"
                        class="pa-1 ma-1"
                        >
                <span class="truncate">{{filter.id}} {{filter.label}}</span> 
                <v-divider></v-divider>
            </v-list-item>
            <!-- shows the selected items, and removes (because they're already selected) them on click -->
  
        </v-list>    
    </div>
  </div>

</template>

<script>

export default {
  name: 'SelectionList',
  props: {
    selection_type: String,
  },
  data() {
    return {
        selected_items: {'target': [], 'question': []},
        search: "",
        input_box: "",
        list_length: 10,
        sort_types: ['ID', 'Alfabet'],
        selected_sort_type: 'ID',
        buttoncolor: '#3F816C', 

    };
  },
  computed: {
    // make multisearch available
    searchList() {
      return this.search.toLowerCase().split(',');
    },
    // returns a list of targets or questions (depends on listOfItemType) that have one of the search for items in them
    filteredList() {
      return this.listOfItemType.filter((item) => {
        if (this.searchList[0] == "" || this.searchList[-1] == " ") {
          return true
        } else
        if (this.searchList.some((searchItem) => item.label.toLowerCase().includes(searchItem) && searchItem !== '')) {
          return true
        } else 
        if (this.searchList.some((searchItem) => item.id == searchItem)) {
          return true
        } else return false
      }
      );
    },
    sortedFilteredList(){
        if (this.selected_sort_type == 'ID'){
            // const new_obj = {}
            // this.$store.state.home.target_data.forEach((e) => {new_obj[e.id] = e})
            // console.log(JSON.stringify(new_obj, null, 4))
            return this.filteredList.sort((a, b) => a.id - b.id)
        }
        if (this.selected_sort_type == 'Alfabet'){
            return this.filteredList.sort((a, b) => {
                if ( a.label < b.label ){
                    return -1;
                }
                if ( a.label > b.label ){
                    return 1;
                }
                return 0;
            })
        }
    },
    smallerFilteredList(){
        return this.sortedFilteredList.slice(0, this.list_length)
    },
    // returns list of targest or question (depends on selected input_type)
    listOfItemType(){
      if (this.selection_type == 'target'){
        return Object.values(this.$store.state.home.target_data)
      } else 
      if (this.selection_type == 'question'){
        return Object.values(this.$store.state.home.question_data)
      }
    },
    // return true if all filtered items are selected, has to have a set() so you can click the checkbox without an error
    allSelected: {
      get() {return this.filteredList.every((filtered_item) => this.selected_items[this.selection_type].includes(filtered_item)) },
      set(val){}
    },
    // return true if all filtered items are deslected
    noneSelected: {
      get() {return this.filteredList.every((filtered_item) => !this.selected_items[this.selection_type].includes(filtered_item)) },
      set(val){}
    },
    // selectedItemInputField: {
    //   get() {return this.$store.state.home.selected_item_ids},
    //   set (value) {this.$store.commit('setSelectedItemIds', value.split(',').map((e) => {return Number(e)}))}
    //   },
    selectedItemIds: {
      set(value){this.$store.commit('setSelectedItemIds', {value: value, type: this.selection_type}); this.updateInputBox()},
      get(){return this.$store.state.home.selected_item_ids[this.selection_type]}
    },
    // sets the veux store input type from the select tool
    selectedItemsUpdated: function(){
      return this.$store.state.home.selected_items_updated[this.selection_type]
    },
    selectedItems: function(){
        return this.$store.getters.selectedItems
    },
  },
  methods: {
    // set the input box to the selected ids in veux store
    updateInputBox(){
      this.input_box = this.$store.state.home.selected_item_ids[this.selection_type]
    },
    updateSelectedItemsFromInputBox: function(){
        this.$store.commit('setSelectedItemIdsFromInputBox', {value: this.input_box.split(',').map((e) => {return Number(e)}), type: this.selection_type})
    },
    // clear=true in selectAll removes all items from the selected id list (if they are there) 
    // otherwise it selects them
    toggleAll: function(ids) {
      const all_selected = this.allSelected
      if (all_selected){
          this.$store.commit('clearAll', {'ids':ids, 'type': this.selection_type})
      } else {
          this.$store.commit('selectAll', {'ids':ids, 'type': this.selection_type})
      }

    },
    selectAll: function(ids) {
      this.$store.commit('selectAll', {'ids':ids, 'type': this.selection_type})
    },
    clearAll: function(ids) {
      this.$store.commit('clearAll', {'ids':ids, 'type': this.selection_type})
    },
    clickItem: function(id){
        this.$store.commit('clickItem', {id: id, type: this.selection_type}); 
    },
    getNewInputItems: function(){
      this.list_length += 10
    },
    getNewSelectedItems: function(){
      this.selected_length += 10
    },
    handleScrollInput: function({ target: { scrollTop, clientHeight, scrollHeight }}) {
      if (scrollTop + clientHeight + 40 >= scrollHeight) {
        this.getNewInputItems()
      }
    },

  },
  watch: {
    selectedItemsUpdated (new_value, old_value) {
      this.updateInputBox()
      this.$store.commit('setSelectedItemsUpdated', {value: false, type:this.selection_type})
    },
    selectedItems (new_value, old_value){
        this.selected_items = Object.create(new_value)
    }
  }
}
</script>

<style scoped>
.small-box{
  float: left; 
  margin-left: 10px; 
  box-sizing: content-box; 
  border: 2px solid steelblue;
  padding-left: 3px;
  padding-right: 3px;
background: lightsalmon;
}
.scrollable{
  overflow-y: scroll;
}
.list{
  border: 1px solid steelblue;
  padding: 5px;
  margin: 2px;
}
.list-item{
    background: none;
}
.list-item:hover{
    background: '#3F816C',
}
.truncate {
    display: inline-block;
    overflow:hidden;
    text-overflow: ellipsis;
    white-space: pre-line;
 }
</style>